@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: "Inter";
  src: url("/public/fonts/Inter-VariableFont_opsz\,wght.ttf") format("truetype");
  font-weight: 100 900; 
  font-style: normal; 
}

@font-face {
  font-family: 'IBMPlexMono'; 
  src: url('/public/fonts/IBMPlexMono-Medium.ttf') format('truetype');
  font-weight: medium; 
  font-style: medium; 
}

@keyframes shake {
  0% { transform: translate(1px, 1px) rotate(0deg); }
  10% { transform: translate(-1px, -2px) rotate(-1deg); }
  20% { transform: translate(-3px, 0px) rotate(1deg); }
  30% { transform: translate(3px, 2px) rotate(0deg); }
  40% { transform: translate(1px, -1px) rotate(1deg); }
  50% { transform: translate(-1px, 2px) rotate(-1deg); }
  60% { transform: translate(-3px, 1px) rotate(0deg); }
  70% { transform: translate(3px, 1px) rotate(-1deg); }
  80% { transform: translate(-1px, -1px) rotate(1deg); }
  90% { transform: translate(1px, 2px) rotate(0deg); }
  100% { transform: translate(1px, -2px) rotate(-1deg); }
}

.shake:hover {
  animation: shake 0.6s infinite;
}

.floating-info {
    position: fixed;
    left: 2%;
    top: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    z-index: 10;
    background-color: rgba(255, 255, 255, 0.9);
    padding: 0.5rem;
    border-radius: 8px;
}

.floating-name {
    font-size: 1.2rem;
    font-weight: 500;
    color: theme('colors.gradientEnd');
}

.floating-linkedin {
    font-size: 1.5rem;
    color: theme('colors.gradientEnd');
    transition: color 0.3s ease;
}

.floating-linkedin:hover {
    color: theme('colors.gradientMiddle');
}

@media (max-width: 768px) {
    .floating-info {
        display: none;
    }
}
